import { isTMA } from '@telegram-apps/sdk-react';

interface UserData {
  id: number;
  first_name: string;
  last_name?: string;
  username?: string;
  photo_url?: string;
}

//     Telegram WebApp
declare global {
  interface Window {
    Telegram?: {
      WebApp?: {
        initDataUnsafe: {
          user?: {
            id: number;
            first_name: string;
            last_name?: string;
            username?: string;
            photo_url?: string;
          };
        };
        initData?: string;
      };
    };
  }
}

/**
 *     Telegram WebApp
 */
export const getUserData = async (): Promise<UserData> => {
  try {
    console.log('    ');
    
    //     URL (  )
    const urlHash = window.location.hash;
    console.log(' URL hash:', urlHash);
    
    if (urlHash.includes('tgWebAppData')) {
      console.log('  tgWebAppData  URL hash,  ');
      
      //    URL hash
      const hashParams = new URLSearchParams(urlHash.substring(1));
      const tgWebAppData = hashParams.get('tgWebAppData');
      
      if (tgWebAppData) {
        console.log('  tgWebAppData:', tgWebAppData);
        
        try {
          const dataParams = new URLSearchParams(tgWebAppData);
          const userJson = dataParams.get('user');
          
          if (userJson) {
            console.log('   user  tgWebAppData:', userJson);
            const user = JSON.parse(userJson);
            
            console.log('     URL hash:', user);
            
            return {
              id: user.id,
              first_name: user.first_name,
              last_name: user.last_name,
              username: user.username,
              photo_url: user.photo_url
            };
          } else {
            console.log('  user    tgWebAppData');
          }
        } catch (err) {
          console.error('    tgWebAppData  URL:', err);
        }
      }
    }
    
    // ,     Telegram
    console.log('     Telegram...');
    const isTelegram = await isTMA('complete');
    console.log(`   isTMA: ${isTelegram}`);
    
    if (!isTelegram) {
      //    Telegram   ID
      console.log('    Telegram,   user_id');
      return getMockUserData();
    }
    
    //  Telegram Web App     window.Telegram.WebApp
    const telegramWebApp = window.Telegram?.WebApp;
    console.log('    WebApp:', !!telegramWebApp);
    
    if (telegramWebApp) {
      console.log('   initDataUnsafe:', !!telegramWebApp.initDataUnsafe);
      console.log('   initData:', !!telegramWebApp.initData);
      
      if (telegramWebApp.initDataUnsafe && telegramWebApp.initDataUnsafe.user) {
        const user = telegramWebApp.initDataUnsafe.user;
        console.log(`   Telegram ID : ${user.id}`);
        console.log(`  : ${user.first_name} ${user.last_name || ''}`);
        
        return {
          id: user.id,
          first_name: user.first_name,
          last_name: user.last_name,
          username: user.username,
          photo_url: user.photo_url
        };
      }
      
      //   initData   
      if (telegramWebApp.initData) {
        console.log('     initData:', telegramWebApp.initData);
        try {
          const searchParams = new URLSearchParams(telegramWebApp.initData);
          const userParam = searchParams.get('user');
          
          if (userParam) {
            console.log('   user  initData:', userParam);
            const user = JSON.parse(userParam);
            console.log(`    initData: ${user.id}`);
            console.log(`  : ${user.first_name} ${user.last_name || ''}`);
            
            return {
              id: user.id,
              first_name: user.first_name,
              last_name: user.last_name,
              username: user.username,
              photo_url: user.photo_url
            };
          }
          console.log('     user  initData');
          
          //    
          console.log('    initData:');
          for (const [key, value] of searchParams.entries()) {
            console.log(`   - ${key}: ${value}`);
          }
        } catch (err) {
          console.error('    initData:', err);
        }
      }
    }
    
    console.log('       Telegram WebApp,   user_id');
    return getMockUserData();
  } catch (error) {
    console.error('     :', error);
    return getMockUserData();
  }
};

/**
 *      
 */
const getMockUserData = (): UserData => {
  //     ID,       
  const testUserId = 999999;
  console.log(`   ID: ${testUserId}`);
  
  return {
    id: testUserId,
    first_name: '',
    last_name: '',
    username: 'test_user',
    photo_url: undefined
  };
}; 